<template>
  <div class="login">
    <!-- 头部 -->
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)"/>
    <!-- 主体 -->
     <div class="container">
      <div class="header">
        <h3>手机号登陆</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="body">
        <div class="phone">
          <input type="text" placeholder="请输入手机号码" v-model="mobile">
        </div>

        <div class="img-check">
          <input type="text" placeholder="请输入图形验证码" v-model="picCode">
          <img :src="picUrl" alt="" v-if="picUrl" @click="getPicCode">
        </div>

        <div class="text-check">
          <input type="text" placeholder="请输入短信验证码" v-model="smsCode">
          <span @click="getCode">{{ second===totalSecond?'获取验证码':+second+'秒后重新发送' }}</span>
        </div>
      </div>
      <div class="footer">
        <button @click="login">登录</button>
      </div>
     </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'
// import { Toast } from 'vant'

export default {
  name: 'LoginIndex',
  data () {
    return {

      picKey: '', // 请求传递的图形验证码唯一标识
      picUrl: '', // 存储请求渲染的图片地址
      totalSecond: 5, // 总秒数
      second: 5, // 当前秒数，开定时器对second--
      timer: null, // 定时器id
      mobile: '', // 手机号
      picCode: '', // 用户输入的图形验证码
      smsCode: '' // 短信验证码
    }
  },
  created () {
    this.getPicCode()
  },

  methods: {
    // 获取图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      this.picKey = key // 存储唯一标识
      this.picUrl = base64 // 存储地址

      // Toast('成功')
    },

    // 获取短信验证码
    async getCode () {
      // 如果没通过校验，则下面的不会执行
      if (!this.validFn()) {
        return
      }

      // 当目前没有定时器开着，且totalSecond与second一致才可以倒计时
      if (!this.timer && this.totalSecond === this.second) {
        // 发送请求
        await getMsgCode(this.picCode, this.picKey, this.mobile)
        // console.log(res)

        this.$toast('短信发送成功，注意查收')

        // 开启倒计时
        this.timer = setInterval(() => {
          this.second--
          console.log('...')

          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null // 重置定时器id
            this.second = this.totalSecond // 归位
          }
        }, 1000)
      }
    },

    // 校验手机号和图形验证码是否合法
    // 通过校验，返回true；不通过校验，返回false
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },

    // 登录
    async login () {
      // 如果没通过校验，则下面的不会执行
      if (!this.validFn()) {
        return
      }

      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('请输入正确的短信验证码')
        return
      }

      const res = await codeLogin(this.mobile, this.smsCode)
      // console.log(res.data)

      this.$store.commit('user/setUserInfo', res.data)

      this.$toast('登录成功')

      // 判断地址栏有无会回跳地址，即有无参数
      // 1.有 => 说明是其他页面，拦截到登录页面的，需要回跳
      // 2.无 => 正常去首页
      const backUrl = this.$route.query.backUrl || '/'
      this.$router.replace(backUrl)
    }

  },

  // 离开页面清除定时器
  destroyed () {
    clearInterval(this.timer)
  }

}
</script>

<style>
input{
  border: 0;
}
.container{
  margin: 50px 30px 0 30px;
}
.header{
  margin-bottom:45px;
}
.header h3{
  font-weight: 400;
  font-size: 25px;
  margin-bottom: 10px;
}
.header p{
  color: #aaa;
  font-size: 14px;
}

.body{
  margin-bottom: 50px;
}

.phone,
.img-check,
.text-check{
  position: relative;
  height: 60px;
  border-bottom: 1px solid #eee;
  padding-top: 20px;

}
.body input{
  height: 100%;
  font-size: 14px;
}

.img-check img{
  position: absolute;
  top: 20px;
  right: 5px;
  width: 80px;
  height: 30px;
  /* border: 1px solid #ccc; */
}

.text-check span{
  position: absolute;
  top: 30px;
  right: 5px;
  color: rgb(216, 159, 52);
  font-size: 13px;
}

.footer button{
  width: 100%;
  height: 38px;
  color: #fff;
  text-align: center;
  line-height: 38px;
  border-radius: 25px;
  border: 0;
  background: linear-gradient(to left,orange,rgb(245, 196, 122))
}
</style>
